import React from 'react'
import {Container,Title} from '../common'
import {injectIntl,FormattedMessage} from 'gatsby-plugin-intl'
import config from '../../../config/default.json'
const WhyRealItems = config.whyRealItems;
const BlockItems = ({k,t,d}) => {
    return (
        <div className="mb-8 md:w-1/2 md:px-4 lg:w-1/4 lg:mb-0">
            <img src={`/img/i--0${k}.png`} alt="" className="mx-auto mb-2"/>
            <div className="mb-2 text-center text-14 text-lights-bluebold font-sans font-bold">{t}</div>
            <div className="font-sans text-13 text-lights-bluebold i-block1-box">{d}</div>
        </div>
    )
}
const Block1 = ({intl}) => {
    return (
        <Container classNames="bg-lights-grayblack">
            <Title>
                <FormattedMessage
                    id="i.block1.i"
                    defaultMessage="I Why "
                />
                <span className="text-lights-orangebold"><FormattedMessage
                    id="i.block1.r"
                    defaultMessage='"Real"'
                /></span>
            </Title>
            <div className="text-center md:w-4/5 mx-auto mb-16 text-lights-bluesuperbold">
                <p className="mb-0 font-sans text-14">
                    <FormattedMessage id="i.block1.title-1" defaultMessage='Why is the "REAL" community.'/>
                </p>
                <p className="mb-0 font-sans text-14">
                    <FormattedMessage id="i.block1.title-2" defaultMessage="If you have an additional $ 20, it will not make you rich, and losing $ 20 will not make you poor either, but you will have the opportunity to receive 1000 ETH from the support of the community and you are a member of the community. "/>
                    <span className="font-semibold">
                        <FormattedMessage id="i.block1.title-3" defaultMessage="REAL here is:"/>
                    </span>
                </p>
            </div>
            <div className="md:flex md:flex-wrap md:-mx-4">
                {WhyRealItems.map((val,key)=>{
                    return <BlockItems key={key} k={key+1} t={intl.formatMessage({id: `i.block1.t-${key+1}`,defaultMessage: val.title})} d={intl.formatMessage({id: `i.block1.d-${key+1}`,defaultMessage: val.desc})}/>
                })}                
            </div>
        </Container>
    )
}
export default injectIntl(Block1)